<script lang="ts">
    import project from '@app/stores/project';
    import theme, { ThemeMode } from '@stores/theme';
    import { getContext } from 'svelte';
    import {
        ButtonIcon,
        ButtonSwitch,
        Tempo,
        GlobalTimer,
        Slider,
    } from './components';

    let { toggle, current, _ } = getContext('theme');

    let playing: boolean = false;

    let globalTimerTime: number = 100;

    // Todo remove = true, it's only used for easier debugging
    let buttonSwitchViewMode: boolean = false;

    let tempoTempo: number = 96;

    let sliderVolume: number = 50;

    $: set_volume_callback(sliderVolume);
    $: set_tempo_callback(tempoTempo);

    $: playButtonIcon = playing ? 'pause' : 'play';

    function toggleTheme() {
        toggle();

        theme.toggleTheme();
    }

    function playButtonClicked(): void {
        playing = !playing;
        if (playing) {
            project.play();
        }
        console.log(playButtonIcon);
    }

    function stopButtonClicked(): void {
        project.stop();
        playing = false;
        console.log('Stop Button Clikced');
    }

    function buttonSwitchedClicked(): void {
        console.log(buttonSwitchViewMode ? 'graph' : 'timeline');
    }

    function set_volume_callback(volume: number) {
        project.set_volume(volume);
    }

    function set_tempo_callback(tempo: number) {
        project.set_tempo(tempo);
    }
</script>

<nav>
    <div class="group left">
        <div class="subgroup toolbar-title">
            {#if $current === 'light'}
                <img
                    alt="logo"
                    on:click={toggleTheme}
                    src="assets/logo-dark.svg"
                />
            {:else}
                <img
                    alt="logo"
                    on:click={toggleTheme}
                    src="assets/logo-light.svg"
                />
            {/if}
        </div>
    </div>
    <div class="group middle">
        <div class="subgroup control-buttons">
            <ButtonIcon
                icontype={playButtonIcon}
                on:click={playButtonClicked}
            />
            <ButtonIcon icontype="stop" on:click={stopButtonClicked} />
        </div>
        <div class="subgroup time-group">
            <GlobalTimer time={globalTimerTime} />
        </div>
        <div class="subgroup view-group">
            <ButtonSwitch
                bind:viewBool={buttonSwitchViewMode}
                on:click={buttonSwitchedClicked}
            />
        </div>
    </div>
    <div class="group right">
        <div class="subgroup right-controls">
            <Tempo bind:tempo={tempoTempo} />
            <Slider bind:volume={sliderVolume} />
        </div>
    </div>
</nav>

