<script lang="ts">
    import type { DropzoneOptions } from '../../../actions';
    import { dropZone } from '../../../actions';
    import Node from './components/Node.svelte';
    import project from '../../../stores/project';
    import { shift_key_pressed } from '../../../actions/key-listener';
    import { InputPort } from './components';
    import Link from './components/links/Link.svelte';
    import { spring } from 'svelte/motion';
    import type { Point } from '@app/utils/geom';
    import type { NodeId } from '@engine';
    import OutputPort from './components/links/io/OutputPort.svelte';

    let graphPanel;

    // $: nodes = $project?.nodes;

    function callback(id, x, y) {
        x -= graphPanel.offsetLeft;
        y -= graphPanel.offsetTop;
        project.create_node(id, x, y);
    }

    let track_arr = [];

    project.subscribe(($project: any) => {
        track_arr = $project.tracks ?? [];
        console.log($project);
    });

    let nodeArray: Array<any> = [];
    let pointerPos: Point = { x: 0, y: 0 };
    let wireVisible = false;
    let wireSource: any;
    let wireStartPos = { x: 0, y: 0 };
    let viewportRect: DOMRect;
    let graph_nodes = [];

    project.subscribe((project_v: any) => {
        graph_nodes = project_v.nodes;
    });

    function toViewportSpace(point): Point {
        viewportRect = graphPanel.getBoundingClientRect();
        return {
            x: point.x - viewportRect.left,
            y: point.y - viewportRect.top,
        };
    }

    function handle_wireout(e) {
        let bounds = e.detail.outputPort.getBoundingClientRect();
        let p = toViewportSpace(bounds);

        wireSource = e.detail;
        wireStartPos = {
            x: p.x + bounds.width / 2,
            y: p.y + bounds.height / 2,
        };
        wireVisible = true;
    }

    function handle_wiretake(e) {
        return;
        let node_id = e.detail.node_id;
        let output_arr = mock_graph_co.get(node_id);

        if (!output_arr) return;
        let output = get_connection_from_iid(output_arr, e.detail.input.id);
        let bounds = output.outputPortDom.getBoundingClientRect();
        let p = toViewportSpace(bounds);

        wireSource = {
            outputData: output.outputPortId,
            outputNodeId: output.outputNodeId,
            outputPort: output.outputPortDom,
        };
        wireStartPos = {
            x: p.x + bounds.width / 2,
            y: p.y + bounds.height / 2,
        };
        wireVisible = true;
    }

    function handle_connect(e) {
        if (wireSource) {
            if (wireSource.track) {
                const input = e.detail;

                project.link_node_track(
                    input.inputNodeId,
                    wireSource.track.id,
                    input.inputData.id
                );

                graph_nodes = graph_nodes;
                wireSource = null;
                wireVisible = false;
            } else {
                const input = e.detail;
                let result = project.link_nodes(
                    input.inputNodeId,
                    wireSource.outputNodeId,
                    input.inputData.id,
                    wireSource.outputData.id
                );

                console.log(
                    result
                        ? 'Successfully linked nodes.'
                        : "Couldn't link nodes."
                );
                graph_nodes = graph_nodes;
                wireSource = null;
                wireVisible = false;
            }
        }
    }

    $: wireEndPos = spring(
        { x: pointerPos.x, y: pointerPos.y },
        {
            stiffness: 0.25,
            damping: 1,
        }
    );
    $: nodeArray;
    $: wireVisible = wireSource != null;
    $: wireSource;
    $: graph_nodes;

    let dropzoneOptions: DropzoneOptions = {
        key: 'nodeObject',
        callback,
    };

    let selected_node = [];
    let selected_node_id = [];
    function selected_node_callback(index, node_id) {
        const is_selected = selected_node_id.includes(index);
        if (shift_key_pressed) {
            if (is_selected) {
                selected_node = selected_node.filter((id) => id !== node_id);
                selected_node_id = selected_node_id.filter(
                    (id) => id !== index
                );
            } else {
                selected_node = [...selected_node, node_id];
                selected_node_id = [...selected_node_id, index];
            }
        } else {
            if (is_selected) {
                selected_node = [];
                selected_node_id = [];
            } else {
                selected_node = [node_id];
                selected_node_id = [index];
            }
        }
    }

    function move_node_callback(node_id: NodeId, x: number, y: number) {
        project.set_node_position(node_id, x, y);
    }

    function handleKeydown(event) {
        if (event.key === 'Delete') {
            for (const node_id of selected_node) {
                project.delete_node(node_id);
            }
            selected_node = [];
            selected_node_id = [];
        }
    }

    function handle_track_output_wireout(e) {
        let bounds = e.detail.target.getBoundingClientRect();
        let p = toViewportSpace(bounds);

        wireSource = e.detail;
        wireStartPos = {
            x: p.x + bounds.width / 2,
            y: p.y + bounds.height / 2,
        };
        wireVisible = true;
    }

    function is_track_output_linked(
        i,
        c_track,
        track_arr,
        graph_nodes_arr
    ): boolean {
        let result = false;

        graph_nodes_arr.forEach((node) => {
            node.links.forEach((link) => {
                if (link.outputNumeralTrackId != undefined) {
                    if (link.outputNumeralTrackId == c_track.numeral_track_id) {
                        result = true;
                        return;
                    }
                }
            });
        });
        return result;
    }
</script>

<svelte:window on:keydown={handleKeydown} />

<div
    on:pointermove={(e) => {
        let vpp = toViewportSpace({ x: e.x, y: e.y });

        pointerPos = { x: vpp.x, y: vpp.y };
    }}
    on:pointerup={() => (wireSource = null)}
    use:dropZone={dropzoneOptions}
    class="graph-container"
    id="graph-container"
    tabindex="0"
    bind:this={graphPanel}
>
    <div class="track-stack">
        {#each track_arr as track, i}
            <OutputPort
                {track}
                owner_node={undefined}
                is_link={is_track_output_linked(
                    i,
                    track,
                    track_arr,
                    graph_nodes
                )}
                output_index={0}
                on:wireout={handle_track_output_wireout}
            />
        {/each}
    </div>

    {#each graph_nodes as node, i (node.node_id)}
        <Node
            on:connect={handle_connect}
            on:wireout={handle_wireout}
            on:wiretake={handle_wiretake}
            node_id={node.node_id}
            node_id_index={i}
            node_data={node}
            posX={node.x}
            posY={node.y}
            parameters={node.parameters}
            name={node.name}
            inputs={node.inputs}
            outputs={node.outputs}
            selected={selected_node.includes(node.node_id)}
            {graph_nodes}
            on:select={(e) => selected_node_callback(i, node.node_id)}
            on:move={(e) =>
                move_node_callback(node.node_id, e.detail.posX, e.detail.posY)}
        />
    {/each}

    {#if wireVisible}
        <svg class="dragged-wire" class:visible={!!wireSource}>
            <Link source={wireStartPos} target={$wireEndPos} />
        </svg>
    {/if}
</div>

<style lang="scss">
    .graph-container {
        outline: none;
        display: flex;
        position: relative;
        flex-grow: 1;
        margin-top: 23px;
        background-color: var(--theme-colors-background-primary);
    }

    .track-stack {
        padding: 8px;
        display: flex;
        flex-direction: column;
        gap: 98px;
        align-items: flex-start;
    }

    .dragged-wire {
        position: absolute;
        color: red;
        overflow: visible;
        pointer-events: none;
        opacity: 1;
        transition: opacity var(--anim-short);
        &.visible {
            opacity: 1;
        }
    }
</style>
