<script lang="ts">
    import { TimelineTrack, Cursor } from './components';
    import project from '../../../stores/project';
    import { dropZone } from '../../../actions';
    import { shift_key_pressed } from '../../../actions/key-listener';

    export let scrollY;

    let track_arr = [];

    let elem;
    let scale = 100;
    let start_beat: number = 0;
    let end_beat: number;

    $: end_beat = start_beat + 20 * scale;

    project.subscribe(($project: any) => {
        track_arr = $project.tracks ?? [];
    });

    function get_dropzoneOptions(track_id) {
        return {
            key: 'clipObject',
            callback: (object: any, x: number) => {
                const unique_clip = object;
                let bounds = elem.getBoundingClientRect();
                let beat_width = bounds.width / (end_beat - start_beat);
                let pos: number = (x - bounds.x) / beat_width;
                pos = Math.floor(pos);
                project.add_clip_to_track(track_id, unique_clip.index, pos);
            },
        };
    }

    function get_clip_moved_callback(track_id) {
        return (event: any) => {
            const clip = event.detail;
            project.set_clip_position(track_id, clip.index, clip.position);
        };
    }

    function get_clip_deleted(track_id) {
        return (event: any) => {
            const clip = event.detail;
            project.delete_clip_from_track(track_id, clip.index);
        };
    }

    function scroll_callback(e: WheelEvent) {
        e.preventDefault();

        if (shift_key_pressed) {
            const wheelDelta = Math.sign(e.deltaY);

            scale += wheelDelta * 10;
            scale = Math.max(scale, 1);
            console.log(scale);
        } else {
            const wheelDelta = Math.sign(e.deltaX);

            start_beat += wheelDelta * scale;
            start_beat = Math.max(start_beat, 0);
        }
    }
</script>

<div class="timeline-container" on:wheel={scroll_callback}>
    <!-- <Cursor {scrollY} /> -->
    <div class="track-stack">
        {#each track_arr as track (track.id)}
            <div
                class="timeline-track"
                use:dropZone={get_dropzoneOptions(track.id)}
                bind:this={elem}
            >
                <TimelineTrack
                    {start_beat}
                    {end_beat}
                    on:clipMoved={get_clip_moved_callback(track.id)}
                    on:clipDeleted={get_clip_deleted(track.id)}
                    on:clipDuplicated={(event) => {
                        const clip = event.detail;
                        project.duplicate_clip(
                            track.id,
                            clip.from_unique_clip_index,
                            Math.floor(clip.position)
                        );
                    }}
                    clips={track.clips ?? []}
                />
            </div>
        {/each}
    </div>
</div>

<style lang="scss">
    .timeline-container {
        flex-grow: 1;
        position: relative;
        background-color: var(--theme-colors-background-primary);
    }

    .track-stack {
        position: absolute;
        padding: 8px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        width: 100%;
        align-items: center;
        justify-content: center;
    }

    :global(.timeline-track) {
        width: 100%;
    }

    ::-webkit-scrollbar {
        width: 6px;
        border-radius: 16px;
        background: var(--theme-colors-background-contrast);
    }
</style>
