<script lang="ts">
    import { createEventDispatcher } from 'svelte';
    import project from '../../../../stores/project';
    import { Knob } from '../../../shared-components/';
    import { control_key_pressed } from '../../../../actions/key-listener';

    export let track = undefined;
    export let selected = false;
    export let editable = false;
    export let enabled = true;
    export let height = '100px';
    export let topknobvalue = 0;
    export let botknobvalue = 0;
    const dispatch = createEventDispatcher();

    function selectHandler(e) {
        if (control_key_pressed) {
            editable = true;
        } else {
            selected = !selected;
            dispatch('select');
        }
    }

    const remove_self = createEventDispatcher();

    function isSelected() {
        selected = !selected;
        project.set_track_enabled(track.id, selected);
    }

    function handleKeydown(e) {
        if (editable) {
            if (e.key === 'Enter' || e.key === 'Escape') {
                editable = false;
            } else if (e.key === 'Backspace') {
                if (control_key_pressed) track.name = '';
                else track.name = track.name.slice(0, -1);
            } else {
                if (e.key.length === 1) {
                    track.name += e.key;
                }
            }
        }
    }

    $: if (!editable) {
        dispatch('change-trackname', { trackname: track.name });
    }

    function set_track_gain(gain: number) {
        project.set_track_gain(track.id, gain);
    }

    function set_track_pan(pan: number) {
        project.set_track_pan(track.id, pan);
    }

    function enableHandler() {
        enabled = !enabled;
    }
</script>

<svelte:window on:keydown={handleKeydown} />
<div
    class="container"
    style={`--height: ${height};`}
    on:click|stopPropagation|preventDefault={selectHandler}
>
    <div class="trackholder">
        <div
            class="select"
            on:click|stopPropagation|preventDefault={enableHandler}
        >
            {#if enabled}
                <div class="isEnabled" />
            {/if}
        </div>
        <div class="name">
            <span class={editable ? 'edit-mode' : 'read-mode'}
                >{track.name}</span
            >
        </div>
        <div class="knobs">
            <div class="topknob">
                <Knob bind:value={topknobvalue} min={0} max={100} />
            </div>
            <div class="bottomknob">
                <Knob bind:value={botknobvalue} min={0} max={100} />
            </div>
        </div>
    </div>
    {#if selected}
        <div class="shadowfilter">
            <div class="isSelected" />
        </div>
    {/if}
</div>

