import { spring } from 'svelte/motion';
export default function drag(node, p) {
    const params = p.params;
    let elem;
    p.elem_store.subscribe((e) => (elem = e));
    const coordinates = spring({ x: params.posX, y: params.posY }, {
        stiffness: 1,
        damping: 1,
    });
    coordinates.subscribe(($coords) => {
        if (elem != undefined) {
            elem.style.transform = `translate3d(${$coords.x}px, ${$coords.y}px, 0)`;
        }
    });
    node.addEventListener('mousedown', handleMouseDown);
    function handleMouseDown(event) {
        params.mousePosX = event.clientX;
        params.mousePosY = event.clientY;
        window.addEventListener('mousemove', handleMouseMove);
        window.addEventListener('mouseup', handleMouseUp);
    }
    function handleMouseMove(event) {
        const dx = event.clientX - params.mousePosX;
        const dy = event.clientY - params.mousePosY;
        params.mousePosX = event.clientX;
        params.mousePosY = event.clientY;
        coordinates.update(($coords) => {
            let x = $coords.x + dx;
            let y = $coords.y + dy;
            if (x < 0) {
                params.mousePosX -= x;
                x = 0;
            }
            // if (x > params.parentWidth - params.parentLeft)
            //     x = params.parentWidth - params.parentLeft;
            if (y < 0) {
                params.mousePosY -= y;
                y = 0;
            }
            // if (y > params.parentHeight - params.parentTop)
            //     y = params.parentHeight - params.parentTop;
            return {
                x,
                y,
            };
        });
    }
    function handleMouseUp() {
        window.removeEventListener('mousemove', handleMouseMove);
        window.removeEventListener('mouseup', handleMouseUp);
        coordinates.update(($coords) => {
            params.updatePos($coords.x, $coords.y, params.mousePosX, params.mousePosY);
            return $coords;
        });
    }
    return {
        destroy() {
            node.removeEventListener('mousedown', () => console.log('clicks'));
        },
    };
}
