<script lang="ts">
    import Link from '../Link.svelte';
    import type { Point } from '@app/utils/geom';
    import { createEventDispatcher, onDestroy } from 'svelte';
    import output_ports from '@app/stores/output_ports';
    export let link_data = null;
    export let size: number = 8;
    export let color: string = 'var(--color-foreground-2)';
    const dispatch = createEventDispatcher();
    let elem: HTMLElement = null;
    let linkTarget: Point = null;

    let linkTargetUnsub: () => void = null;

    $: if (!link_data) {
        linkTarget = null;
        if (linkTargetUnsub) {
            linkTargetUnsub();
            linkTargetUnsub = null;
        }
    }

    $: if (link_data && elem) {
        if (linkTargetUnsub) {
            linkTargetUnsub();
            linkTargetUnsub = null;
        }

        output_ports.subscribe((ports) => {
            if (link_data && elem) {
                try {
                    let bounds: DOMRect = ports
                        .get(
                            link_data.outputNumeralNodeId
                                ? link_data.outputNumeralNodeId
                                : 'Track' + link_data.outputNumeralTrackId
                        )
                        .get(link_data.outputPortIndex);
                    let local_bounds = elem.getBoundingClientRect();

                    linkTarget = {
                        x: bounds.x - local_bounds.x - 6,
                        y: bounds.y - local_bounds.y - 6,
                    };
                } catch (error) {}
            }
        });
    }

    onDestroy(() => {
        if (linkTargetUnsub) {
            linkTargetUnsub();
        }
    });
</script>

<div
    on:pointerup={(e) => e.button === 0 && dispatch('connect', elem)}
    on:pointerdown={(e) => e.button === 0 && link_data && dispatch('wiretake')}
    on:click|stopPropagation
    bind:this={elem}
>
    <button />

    {#if linkTarget}
        <svg class="link">
            <Link source={{ x: 0, y: 0 }} target={linkTarget} />
        </svg>
    {/if}
</div>

<style lang="scss">
    div {
        width: 12px;
        height: 12px;
        display: inherit;
        position: relative;
    }

    button {
        width: 100%;
        height: 100%;
        border: 2px solid var(--theme-colors-border);
        border-radius: 50%;
        padding: 2px 4px;
        background-color: var(--theme-colors-background-primary);
        &:hover {
            cursor: pointer;
        }
        &:active {
            background-color: var(--theme-colors-border);
        }
    }

    .link {
        color: var(--theme-colors-primary);
        overflow: visible;
        position: absolute;
        top: 6px;
        left: 6px;
        pointer-events: none;
    }
</style>
