<script lang="ts">
    import Knob from '../../../shared-components/Knob.svelte';
    import drag from '../../../../actions/DraggableNode';
    import type { Position } from '../../../../actions/DraggableNode';
    import project from '../../../../stores/project';
    import { onMount } from 'svelte';
    import { InputPort, OutputPort } from '.';
    import { createEventDispatcher } from 'svelte';
    import { get, writable } from 'svelte/store';
    import type { MockConnectionData } from '../';
    import type { NodeId } from '@engine';

    enum NodeControlType {
        Knob = 0,
        Breaker = 1,
    }

    class NodeControl {
        type: NodeControlType;
        id: number;
        object;
    }

    class NodeIO {
        id: number;
        label: string;
    }

    class GridNode {
        node: NodeControl;
        index: number;
        row: number;
        col: number;

        constructor(
            node: NodeControl,
            index: number,
            row: number,
            col: number
        ) {
            this.node = node;
            this.index = index;
            this.row = row;
            this.col = col;
        }
    }

    let node;

    export let node_id: NodeId;
    export let node_id_index = -1;
    export let node_data = null;

    export let parameters: NodeControl[] = [];
    export let inputs: NodeIO[] = [];
    export let outputs: NodeIO[] = [];
    export let name = 'Undefined';
    export let parentid = 'graph-container';
    export let posX = 0;
    export let posY = 0;
    export let graph_nodes = [];

    let clicked = false;
    export let selected = false;

    const dispatch = createEventDispatcher();

    let parent: HTMLElement | null = null;
    let positions: Position = {
        posX,
        posY,
        mousePosX: 0,
        mousePosY: 0,
        parentTop: 0,
        parentLeft: 0,
        parentWidth: 0,
        parentHeight: 0,
        updatePos,
    };
    let elem: HTMLElement = undefined;

    let elem_store = writable(elem);

    $: elem_store.set(elem);

    onMount(() => {
        parent = document.getElementById(parentid);
        positions.parentTop = parent.offsetTop;
        positions.parentLeft = parent.offsetLeft;
        positions.parentWidth = parent.offsetWidth;
        positions.parentHeight = parent.offsetHeight;
    });

    function updatePos(posX_: number, posY_: number) {
        if (posX != posX_ || posY != posY_) {
            posX = posX_;
            posY = posY_;
            dispatch('move', {
                node_id: node_data.node_id,
                posX,
                posY,
            });
        }
    }

    let grid_width: number = 0;
    $: {
        let max_width: number = 0;
        let holder: number = 0;

        parameters.forEach((v, i, a) => {
            console.log({ v, i, a });
            if (v.type == NodeControlType.Breaker) {
                holder = 0;
            } else {
                holder += 1;
            }
            if (holder > max_width) max_width = holder;
        });
        grid_width = max_width;
    }

    let grid_height: number = 0;
    $: {
        let height: number = 1;

        parameters.forEach((v, i, a) => {
            if (v.type == NodeControlType.Breaker) {
                height += 1;
            }
        });
        grid_height = height;
    }

    let parameters_grid_nodes = [];
    $: {
        let current_width = 0;
        let current_height = 0;

        function process_grid(param: NodeControl, index: number) {
            // let index = parameters.indexOf(param);
            let row: number = current_height;
            let col: number = current_width;

            console.log(row, col);

            if (param.type == NodeControlType.Breaker) {
                current_height += 1;
                current_width = 1;
                return null;
            }
            current_width += 1;
            return new GridNode(param, index, row, col);
        }

        parameters_grid_nodes = parameters.map(process_grid);
    }

    function knob_change_value(knod_id, value) {
        project.set_node_param_value(node_id, knod_id, value);
    }

    function mouseMoveHandler(e) {
        clicked = false;
    }

    function mouseDownHandler(e) {
        clicked = true;
    }

    function mouseUpHandler(e) {
        if (clicked == true) {
            selected = !selected;
            dispatch('select');
        }
        clicked = false;
    }

    function get_input_link(input, i, graph_nodes_arr) {
        let link_ret = null;

        graph_nodes_arr.forEach((node) => {
            node.links.forEach((link) => {
                if (
                    node.numeral_node_id == node_data.numeral_node_id &&
                    link.inputPortIndex == i
                ) {
                    link_ret = link;
                    console.log('Input link: ', link);
                    return;
                }
            });
            if (link_ret) return;
        });
        console.log('GetInputLink returns ', link_ret);
        return link_ret;
    }

    function is_output_linked(output_index, graph_nodes): boolean {
        let result = false;

        graph_nodes.forEach((node) => {
            node.links.forEach((link) => {
                if (
                    link.outputNumeralNodeId == node_data.numeral_node_id &&
                    link.outputPortIndex == output_index
                ) {
                    result = true;
                    return;
                }
            });
        });
        return result;
    }
</script>

<div class="node" bind:this={elem} style="--pos-x: {posX}px; --pos-y: {posY}px">
    <div class="input">
        {#each inputs as input, i}
            <InputPort
                link_data={get_input_link(input, i, graph_nodes)}
                on:wiretake={() =>
                    dispatch('wiretake', {
                        input: input,
                        node_id_index: node_id_index,
                    })}
                on:connect={(e) => {
                    dispatch('connect', {
                        inputPort: e.detail,
                        inputNode: elem,
                        inputData: input,
                        inputNodeId: node_data.node_id,
                    });
                }}
            />
        {/each}
    </div>
    <div
        on:mousedown={mouseDownHandler}
        on:mousemove={mouseMoveHandler}
        on:mouseup={mouseUpHandler}
        use:drag={{ params: positions, elem_store }}
        class="container"
        style={`--grid-width: ${grid_width}; --grid-height: ${grid_height}; --border-width: ${
            selected ? '1px' : '0px'
        };`}
    >
        <div class="content">
            <h2>
                {name}
            </h2>
            <div class="grid">
                {#each parameters_grid_nodes as param}
                    {#if param != undefined}
                        <div
                            style="grid-column: {param.col}; grid-row: {param.row};"
                            class="node-content"
                        >
                            {#if param.node.type == NodeControlType.Knob}
                                <div class="node-content-knob">
                                    <Knob
                                        min={param.node.object.min}
                                        max={param.node.object.max}
                                        value={param.node.object.value}
                                        progress_type={param.node.object.kind}
                                        on:change={(e) =>
                                            knob_change_value(
                                                param.index,
                                                e.detail.value
                                            )}
                                    />
                                </div>
                                <h4>{param.node.object.label}</h4>
                            {/if}
                        </div>
                    {/if}
                {/each}
            </div>
        </div>
    </div>

    <div class="output">
        {#each outputs as output, i}
            <OutputPort
                owner_node={node_data}
                output_index={i}
                is_link={is_output_linked(i, graph_nodes)}
                on:wireout={(e) => {
                    dispatch('wireout', {
                        outputNode: elem,
                        outputPort: e.detail,
                        outputData: output,
                        outputNodeId: node_data.node_id,
                    });
                }}
            />
        {/each}
    </div>
</div>

