<script lang="ts">
    import {
        Toolbar,
        ClipPanel,
        NodePanel,
        TimelinePanel,
        GraphPanel,
        TrackPanel,
    } from './components';
    import { ThemeWrapper, ThemeToggle } from 'svelte-themer';
    import { themes } from './themes';
    import view from './stores/view';
    import { onkeydown, onkeyup, onkeypress } from './actions/key-listener';

    window.onkeydown = onkeydown;
    window.onkeyup = onkeyup;
    window.onkeypress = onkeypress;

    let scrollY: number = 0;

    // ask if user really wants to reload the page
    window.onbeforeunload = (e: BeforeUnloadEvent) => {
        e.preventDefault();
        e.returnValue = '';
    };
</script>

<ThemeWrapper {themes}>
    <main>
        <Toolbar />
        <div class="content-container">
            {#if $view}
                <ClipPanel />
            {/if}
            {#if !$view}
                <NodePanel />
            {/if}
            <div
                class="main-content"
                on:scroll={(e) => (scrollY = e.currentTarget.scrollTop)}
            >
                <TrackPanel />
                {#if $view}
                    <TimelinePanel {scrollY} />
                {:else}
                    <GraphPanel />
                {/if}
            </div>
        </div>
    </main>
    <div id="mobile-easteregg">
        <h1>Nema Studio</h1>
        <p>Currently only available on desktop</p>
    </div>
</ThemeWrapper>

