<script lang="ts">
    import { AddTrackButton, Track } from './components';
    import project from '../../../stores/project';
    import { shift_key_pressed } from '../../../actions/key-listener';
    import type { TrackId } from '@engine';

    function add_track_callback() {
        project.add_track('Track Name');
    }

    let tracks = [];

    project.subscribe(($project: any) => {
        if ($project != null) {
            tracks = $project.tracks;
        }
    });

    let selected_tracks = [];
    let selected_tracks_id = [];
    function select_track_callback(index, node_id) {
        const is_selected = selected_tracks_id.includes(index);
        if (shift_key_pressed) {
            if (is_selected) {
                selected_tracks = selected_tracks.filter(
                    (id) => id !== node_id
                );
                selected_tracks_id = selected_tracks_id.filter(
                    (id) => id !== index
                );
            } else {
                selected_tracks = [...selected_tracks, node_id];
                selected_tracks_id = [...selected_tracks_id, index];
            }
        } else {
            if (is_selected) {
                selected_tracks = [];
                selected_tracks_id = [];
            } else {
                selected_tracks = [node_id];
                selected_tracks_id = [index];
            }
        }
    }

    function handleKeydown(event) {
        if (event.key === 'Delete') {
            for (let track of selected_tracks) {
                project.delete_track(track);
            }
            selected_tracks = [];
            selected_tracks_id = [];
        }
    }
</script>

<svelte:window on:keydown={handleKeydown} />
<div class="track-container">
    <div class="track-stack">
        {#each tracks as track, index (track.id)}
            <Track
                {track}
                selected={selected_tracks.includes(track.id)}
                editable={(selected_tracks.includes(track.id), false)}
                on:select={(e) => select_track_callback(index, track.id)}
                on:change-trackname={(e) =>
                    project.set_track_name(track.id, e.detail.trackname)}
            />
        {/each}
    </div>
    <div class="add-track">
        <AddTrackButton on:click={add_track_callback} />
    </div>
</div>

<style lang="scss">
    .track-container {
        display: flex;
        flex-direction: column;
        justify-content: stretch;
        width: 316px;
        padding: 8px;
        background-color: var(--theme-colors-background-primary);
        .track-stack {
            display: flex;
            flex-direction: column;
            gap: 10px;
            z-index: 1;
            align-items: center;
            justify-content: center;
            margin-bottom: 10px;
        }
        .add-track {
            margin-top: auto;
        }
    }
</style>
