<script lang="ts">
    export let tempo: number = 96;
    let min: number = 0.0;
    let max: number = 999.9;
    let ret: number = 0;

    $: tempo = Math.max(min, Math.min(tempo, max));

    function detectTrackPad(e) {
        var isTrackpad = false;
        if (e.wheelDeltaY) {
            if (e.wheelDeltaY === e.deltaY * -3) {
                handleTrackPad(e);
                return;
            }
        } else if (e.deltaMode === 0) {
            handleTrackPad(e);
            return;
        }
        handleWheel(e);
    }

    function handleWheel(e: WheelEvent) {
        const side = Math.sign(e.deltaY);
        const factor = e.shiftKey ? 2 : 1;

        tempo -= side * factor;
    }

    function handleTrackPad(e: WheelEvent) {
        const side = Math.sign(e.deltaY);
        const factor = e.shiftKey ? 2 : 1;

        if (ret === 0) {
            tempo -= side;
        }
        ret += 1;
        if (ret === 5) {
            ret = 0;
        }
    }

    function handlePointerDown(e: PointerEvent) {
        if (e.button !== 0) {
            return;
        }

        const startValue = tempo;
        let deltaY = 0;

        this.onpointermove = (e: PointerEvent) => {
            deltaY += e.movementY / 2;

            tempo = startValue - Math.round(deltaY / 4);
        };

        this.onpointerup = (e: PointerEvent) => {
            this.onpointermove = null;
            this.onpointerup = null;
            this.releasePointerCapture(e.pointerId);
        };
        this.setPointerCapture(e.pointerId);
    }
</script>

<div class="frame" on:wheel={detectTrackPad} on:pointerdown={handlePointerDown}>
    <div class="tempo">
        <span>
            {tempo}
        </span>
        <span class="little">.00</span>
    </div>
