<script lang="ts">
    export let time: number = 0.0;
    export let tempo: number = 96;
    export let signature: number = 4;
    export let is_bar: boolean = false;

    $: min = Math.floor(time / 60);
    $: sec = Math.floor(time - min * 60);
    $: rest = Math.floor((time - min * 60 - sec) * 100);

    $: bps = 60 / tempo;
    $: bars = Math.floor(time / (signature * bps)) + 1;
    $: steps = Math.floor((time - (bars - 1) * signature * bps) / bps) + 1;
    $: ticks = Math.floor(
        (((time - (bars - 1) * signature * bps) % bps) * 24) / bps
    );

    function pad_with_zero(x: number) {
        return x < 10 ? '0' + x : x;
    }

    $: bst_string = `${bars}:${pad_with_zero(steps)}`;
    $: ticks_string = `:${pad_with_zero(ticks)}`;

    $: time_str = `${min}:${pad_with_zero(sec)}`;
    $: rest_str = `:${pad_with_zero(rest)}`;

    function change_mode() {
        is_bar = !is_bar;
    }
</script>

<div class="frame" on:click={change_mode}>
    {#if is_bar}
        <div class="content">
            <p>{bst_string}</p>
            <p class="little">{ticks_string}</p>
        </div>
        <span>Bar</span>
    {:else}
        <div class="content">
            <p>{time_str}</p>
            <p class="little">{rest_str}</p>
        </div>
        <span>Min</span>
    {/if}
</div>

