import { derived, writable } from 'svelte/store';
import init_wasm from '@engine';
const project = writable();
function add_buffer(buffer) {
    return new Promise((resolve, reject) => {
        project.update(($project) => {
            $project?.create_buffer(buffer).then(resolve).catch(reject);
            return $project;
        });
    });
}
function add_track(name) {
    let track_id;
    project.update(($project) => {
        track_id = $project.create_track(name);
        return $project;
    });
    return track_id;
}
async function add_unique_clips(clip_names, buffers) {
    project.update(($project) => {
        (async () => {
            for (let i = 0; i < clip_names.length; i++) {
                const buffer = buffers[i];
                const clip_name = clip_names[i];
                try {
                    const buffer_id = await $project.create_buffer(buffer);
                    const buffer_duration = $project.get_buffer_duration(buffer_id);
                    const clip_id = $project.create_unique_clip(clip_name, buffer_duration, buffer_id);
                }
                catch (e) {
                    console.log("Couldn't add clip:", clip_name);
                }
            }
            project.set($project);
        })();
        return $project;
    });
}
function set_clip_buffer(clip_id, buffer_id) {
    project.update(($project) => {
        $project.set_clip_buffer(clip_id, buffer_id);
        return $project;
    });
}
function delete_track(track_id) {
    project.update(($project) => {
        $project?.delete_track(track_id);
        return $project;
    });
}
function set_track_gain(track_id, gain) {
    project.update(($project) => {
        $project.set_track_gain(track_id, gain);
        return $project;
    });
}
function set_track_pan(track_id, pan) {
    project.update(($project) => {
        $project.set_track_pan(track_id, pan);
        return $project;
    });
}
function set_track_enabled(track_id, enabled) {
    project.update(($project) => {
        $project.set_track_enabled(track_id, enabled);
        return $project;
    });
}
function set_clip_position(track_id, clip_index, position) {
    position = Math.round(position);
    project.update(($project) => {
        const clip_id = $project.get_track_clip(track_id, clip_index);
        $project.set_clip_position(clip_id, BigInt(position));
        return $project;
    });
}
function set_clip_duration(track_id, clip_index, duration) {
    duration = Math.round(duration);
    project.update(($project) => {
        const clip_id = $project.get_track_clip(track_id, clip_index);
        $project.set_clip_duration(clip_id, BigInt(duration));
        return $project;
    });
}
function add_clip_to_track(track_id, unique_clip_index, pos, dur) {
    let copy_clip_id;
    project.update(($project) => {
        const unique_clip_id = $project.get_unique_clip_id(unique_clip_index);
        const clip_name = $project.get_unique_clip_name(unique_clip_id);
        copy_clip_id = $project.copy_clip(clip_name, BigInt(pos), unique_clip_id);
        if (dur) {
            $project.set_clip_duration(copy_clip_id, BigInt(dur));
        }
        $project.add_clip_to_track(track_id, copy_clip_id);
        return $project;
    });
    return copy_clip_id;
}
function duplicate_clip(track_id, unique_clip_index, pos) {
    project.update(($project) => {
        const unique_clip_id = $project.get_unique_clip_id(unique_clip_index);
        const name = $project.get_unique_clip_name(unique_clip_id);
        let copy_clip_id = $project.copy_clip(name, BigInt(pos), unique_clip_id);
        $project.add_clip_to_track(track_id, copy_clip_id);
        return $project;
    });
}
function delete_clip_from_track(track_id, clip_index) {
    project.update(($project) => {
        const clip_id = $project.get_track_clip(track_id, clip_index);
        $project.remove_clip(track_id, clip_id);
        $project.delete_clip(clip_id);
        return $project;
    });
}
function set_volume(volume) {
    project.update(($project) => {
        $project?.set_volume(volume);
        return $project;
    });
}
function set_tempo(tempo) {
    project.update(($project) => {
        $project?.set_tempo(tempo);
        return $project;
    });
}
function set_node_position(node_id, x, y) {
    project.update(($project) => {
        $project?.set_node_position_by_id(node_id, x, y);
        return $project;
    });
}
function set_node_param_value(node_id, param_id, value) {
    project.update(($project) => {
        const module_instance_id = $project.get_node_module_instance_id(node_id);
        $project?.set_module_instance_control_param_value(module_instance_id, param_id, value);
        return $project;
    });
}
function set_track_name(track_id, name) {
    project.update(($project) => {
        $project?.set_track_name(track_id, name);
        return $project;
    });
}
const project_data = derived(project, ($project, set) => {
    if (!$project) {
        return set({
            tracks: [],
            module_names: [],
            clips: [],
            unique_clips: [],
            nodes: [],
            volume: 0.0,
            tempo: 0.0,
            quartz_to_secs: (_) => 0,
            secs_to_quartz: (_) => 0n,
        });
    }
    function get_track_clip(track_id, clip_index) {
        const clip_id = $project.get_track_clip(track_id, clip_index);
        if (clip_id && $project.is_clip_alive(clip_id)) {
            return {
                id: clip_id,
                from_unique_clip_index: $project.get_clip_unique_clip_id_index(clip_id),
                index: clip_index,
                name: $project.get_clip_name(clip_id),
                position: Number($project.get_clip_position(clip_id)),
                duration: Number($project.get_clip_duration(clip_id)),
                buffer: [], // no getter for buffer
            };
            // C'est bon j'ai finis cf project/clip.rs
        }
    }
    function nodeid_to_number(node_id) {
        return $project.node_id_to_number(node_id);
    }
    function trackid_to_number(track_id) {
        return $project.track_id_to_number(track_id);
    }
    function number_to_nodeid(id) {
        return $project.number_to_node_id(id);
    }
    function nodeid_equals(id1, id2) {
        return $project.node_id_equals(id1, id2);
    }
    function get_track_clips(track_id) {
        const clips_count = $project.get_track_clip_count(track_id);
        const clips = [...Array(clips_count).keys()].map((i) => {
            const clip = get_track_clip(track_id, i);
            return clip;
        });
        return clips;
    }
    function get_track(track_id) {
        const name = $project.get_track_name(track_id);
        const clips = get_track_clips(track_id);
        const gain = $project.get_track_gain(track_id);
        const pan = $project.get_track_pan(track_id);
        const is_enabled = $project.is_track_enabled(track_id);
        return {
            id: track_id,
            numeral_track_id: trackid_to_number(track_id),
            name,
            clips,
            gain,
            pan,
            is_enabled,
        };
    }
    function get_clip(clip_id, index) {
        const name = $project.get_clip_name(clip_id);
        const position = $project.get_clip_position(clip_id);
        const duration = $project.get_clip_duration(clip_id);
        const from_unique_clip_index = $project.get_clip_unique_clip_id_index(clip_id);
        return {
            from_unique_clip_index,
            id: clip_id,
            index,
            name,
            position: Number(position),
            duration: Number(duration),
        };
    }
    function get_unique_clip(clip_id, index) {
        const name = $project.get_unique_clip_name(clip_id);
        const duration = $project.get_unique_clip_duration(clip_id);
        return {
            id: clip_id,
            index,
            name,
            duration: Number(duration),
        };
    }
    function get_module_factory_names($project) {
        const modules_count = $project.get_module_factory_id_count();
        const modules = [];
        for (let i = 0; i < modules_count; i++) {
            const module_id = $project.get_module_factory_id(i);
            modules.push({
                id: i,
                name: $project.get_module_factory_name(module_id),
            });
        }
        return modules;
    }
    function get_nodes($project) {
        const nodes_count = $project.get_node_ids_count();
        const nodes = [];
        for (let i = 0; i < nodes_count; i++) {
            const node_id = $project.get_node_id(i);
            const node_name = $project.get_node_name(node_id);
            const node_position_x = $project.get_node_x(node_id);
            const node_position_y = $project.get_node_y(node_id);
            const module_instance_id = $project.get_node_module_instance_id(node_id);
            let inputs = [];
            let outputs = [];
            let params = [];
            let links = [];
            const control_count = $project.get_module_instance_control_count(module_instance_id);
            for (let j = 0; j < control_count; j++) {
                const control_type = $project.get_module_instance_control_type(module_instance_id, j);
                if (control_type == 'input') {
                    const input_id = inputs.length;
                    const input_label = $project.get_module_instance_control_input_label(module_instance_id, input_id);
                    inputs.push({
                        id: input_id,
                        label: input_label,
                    });
                }
                else if (control_type == 'output') {
                    const output_id = outputs.length;
                    const output_label = $project.get_module_instance_control_output_label(module_instance_id, output_id);
                    outputs.push({
                        id: output_id,
                        label: output_label,
                    });
                }
                else if (control_type == 'param') {
                    const param_id = params.length;
                    const param_label = $project.get_module_instance_control_param_label(module_instance_id, param_id);
                    const param_default_value = $project.get_module_instance_control_param_default_value(module_instance_id, param_id);
                    const param_min_value = $project.get_module_instance_control_param_min_value(module_instance_id, param_id);
                    const param_max_value = $project.get_module_instance_control_param_max_value(module_instance_id, param_id);
                    const param_value = $project.get_module_instance_control_param_value(module_instance_id, param_id);
                    params.push({
                        type: 0,
                        id: param_id,
                        object: {
                            label: param_label,
                            max: param_max_value,
                            min: param_min_value,
                            default: param_default_value,
                            value: param_value,
                        },
                    });
                }
            }
            inputs.forEach((input, i) => {
                let result = $project.get_link(node_id, i);
                if (result) {
                    console.log(result);
                    if (result.node) {
                        links.push({
                            outputNodeId: result.node,
                            outputNumeralNodeId: nodeid_to_number(result.node),
                            inputPortIndex: i,
                            outputPortIndex: result.output_index,
                        });
                    }
                    else if (result.track) {
                        console.log('LINK TO TRCAKCJ:', result.track);
                        links.push({
                            outputTrackId: result.track,
                            outputNumeralTrackId: trackid_to_number(result.track),
                            inputPortIndex: i,
                            outputPortIndex: result.output_index,
                        });
                    }
                }
            });
            const node = {
                node_id: node_id,
                numeral_node_id: nodeid_to_number(node_id),
                x: node_position_x,
                y: node_position_y,
                name: node_name,
                parameters: params,
                inputs: inputs,
                outputs: outputs,
                links: links,
            };
            nodes.push(node);
        }
        return nodes;
    }
    function get_tracks($project) {
        let tracks = [];
        const tracks_count = $project.get_track_count();
        for (let i = 0; i < tracks_count; i++) {
            let track_id = $project.get_track_by_index(i);
            tracks.push(get_track(track_id));
        }
        return tracks;
    }
    function get_clips($project) {
        let clips = [];
        const clips_count = $project.get_clip_ids_count();
        for (let i = 0; i < clips_count; i++) {
            const clip_id = $project.get_clip_id(i);
            clips.push(get_clip(clip_id, i));
        }
        return clips;
    }
    function get_unique_clips($project) {
        let unique_clips = [];
        const unique_clips_count = $project.get_unique_clip_ids_count();
        for (let i = 0; i < unique_clips_count; i++) {
            const clip_id = $project.get_unique_clip_id(i);
            unique_clips.push(get_unique_clip(clip_id, i));
        }
        return unique_clips;
    }
    function get_volume($project) {
        return $project.volume();
    }
    function get_tempo($project) {
        return $project.tempo();
    }
    set({
        tracks: get_tracks($project),
        clips: get_clips($project),
        unique_clips: get_unique_clips($project),
        module_names: get_module_factory_names($project),
        nodes: get_nodes($project),
        volume: get_volume($project),
        tempo: get_tempo($project),
        quartz_to_secs: (q) => $project.quartz_to_secs(q),
        secs_to_quartz: (s) => $project.secs_to_quartz(s),
    });
});
function play() {
    project.update(($project) => {
        $project.play();
        return $project;
    });
}
function stop() {
    project.update(($project) => {
        $project.stop();
        return $project;
    });
}
