function create_node(module_factory_index, x, y) {
    let module_id;
    let module_instance_id;
    let node_id;
    project.update(($project) => {
        module_id = $project?.get_module_factory_id(module_factory_index);
        module_instance_id = $project?.create_module_instance(module_id);
        node_id = $project?.create_node(module_id, module_instance_id, x, y);
        return $project;
    });
    return node_id;
}
