function link_nodes(first_node_id, second_node_id, first_node_index, second_node_index) {
    let result = false;
    project.update(($project) => {
        result = $project.link_nodes(first_node_id, second_node_id, first_node_index, second_node_index);
        return $project;
    });
    return result;
}
function link_node_track(node_id, track_id, index) {
    project.update(($project) => {
        $project.link_node_to_track(node_id, track_id, index);
        return $project;
    });
}
