<script context="module" lang="ts">
    import { watchResize } from 'svelte-watch-resize';
    import { control_key_pressed } from '../../../../actions/key-listener';

    export interface Clip {
        id: number;
        position: number;
        duration: number;
        name: string;
    }

    function roundRect(
        ctx: CanvasRenderingContext2D,
        x: number,
        y: number,
        width: number,
        height: number,
        radius: number
    ) {
        ctx.moveTo(x + radius, y);
        ctx.lineTo(x + width - radius, y);
        ctx.quadraticCurveTo(x + width, y, x + width, y + radius);
        ctx.lineTo(x + width, y + height - radius);
        ctx.quadraticCurveTo(
            x + width,
            y + height,
            x + width - radius,
            y + height
        );
        ctx.lineTo(x + radius, y + height);
        ctx.quadraticCurveTo(x, y + height, x, y + height - radius);
        ctx.lineTo(x, y + radius);
        ctx.quadraticCurveTo(x, y, x + radius, y);
    }
</script>

<script lang="ts">
    import { createEventDispatcher, onMount } from 'svelte';
    import theme from '@stores/theme';

    export let track_height = 100;
    export let start_beat: number = 0;
    export let end_beat: number = 20;
    export let signature: number = 4;
    export let clips: Clip[] = [];
    let selected_clip = -1;

    let width: number;
    let height: number;

    let cvs: HTMLCanvasElement;
    let ctx: CanvasRenderingContext2D;

    const dispatcher = createEventDispatcher();

    let bck_contrast = getComputedStyle(
        document.documentElement
    ).getPropertyValue('--theme-colors-background-contrast');
    let bck_primary = getComputedStyle(
        document.documentElement
    ).getPropertyValue('--theme-colors-background-primary');
    let primary_color = getComputedStyle(
        document.documentElement
    ).getPropertyValue('--theme-colors-primary');
    let primary_dark = getComputedStyle(
        document.documentElement
    ).getPropertyValue('--theme-colors-primary_dark');
    let border_color = getComputedStyle(
        document.documentElement
    ).getPropertyValue('--theme-colors-border');

    theme.subscribe(($theme: any) => {
        bck_contrast = $theme.colors.background.contrast;
        bck_primary = $theme.colors.background.primary;
        primary_color = $theme.colors.primary;
        redraw();
    });

    $: {
        // When any of these change, call redraw
        start_beat;
        end_beat;
        clips;
        width;
        height;
        signature;

        redraw();
    }

    function getRealMousePosition(e) {
        var mouseX = e.offsetX * (cvs.width / cvs.clientWidth);
        var mouseY = e.offsetY * (cvs.height / cvs.clientHeight);

        return { x: mouseX, y: mouseY };
    }

    function redraw() {
        if (cvs) {
            ctx = ctx ?? cvs.getContext('2d', { alpha: false });
            cvs.width = cvs.clientWidth;
            cvs.height = cvs.clientHeight;

            drawBackground();
            drawClips();
        }
    }

    function drawBackground() {
        ctx.save();
        {
            ctx.scale(1, cvs.height);
            ctx.stroke();
            ctx.fillStyle = `${bck_contrast}`;

            ctx.fillRect(0, 0, cvs.width, cvs.height);
            drawDivisions(ctx);
        }
        ctx.restore();
    }

    function drawSubDivisions(ctx: CanvasRenderingContext2D) {
        const view_range = end_beat - start_beat;
        const beat_width = cvs.width / view_range;
        const start_offset = (start_beat % 1) * beat_width;

        ctx.beginPath();
        for (let i = 0; i < view_range * (signature + 1); i++) {
            const x = (i * beat_width) / (signature + 1) - start_offset;

            ctx.moveTo(x, 0);
            ctx.lineTo(x, 1);
        }
        ctx.strokeStyle = `${bck_contrast}`;
        ctx.lineWidth = 1.0;
        ctx.globalAlpha = 0.5;
        ctx.stroke();
    }

    function drawDivisions(ctx: CanvasRenderingContext2D) {
        const view_range = end_beat - start_beat;
        const beat_width = cvs.width / view_range;
        const start_offset = (start_beat % 1) * beat_width;

        if (beat_width > 60) {
            drawSubDivisions(ctx);
        }
        ctx.beginPath();
        for (let i = 0; i < view_range; i++) {
            const x = i * beat_width - start_offset;

            ctx.moveTo(x, 0);
            ctx.lineTo(x, 1);
        }
        ctx.strokeStyle = `${bck_contrast}`;
        ctx.lineWidth = 2.0;
        ctx.globalAlpha = 0.5;
        ctx.stroke();
    }

    function drawClips() {
        const beat_width = cvs.width / (end_beat - start_beat);

        for (const clip of clips) {
            const clip_start_beats = clip.position;
            const clip_duration_beats = clip.duration;
            const clip_width = clip_duration_beats * beat_width;
            const index = clips.indexOf(clip);
            ctx.save();
            {
                ctx.translate((clip_start_beats - start_beat) * beat_width, 0);

                if (index == selected_clip) {
                    ctx.fillStyle = `${primary_dark}`;
                } else {
                    ctx.fillStyle = `${primary_color}`;
                }
                ctx.beginPath();
                roundRect(ctx, 0, 0, clip_width, cvs.height, 16);
                ctx.fill();

                // border
                ctx.strokeStyle = 'black';
                ctx.lineWidth = 2.0;
                ctx.stroke();

                ctx.fillStyle = `${bck_primary}`;
                ctx.textBaseline = 'top';
                ctx.font = '14px sans-serif';
                ctx.fillText(
                    displayName(clip.name, clip_width),
                    8,
                    8,
                    clip_width
                );
            }
            ctx.restore();
        }
    }

    function displayName(text: string, space: number) {
        let metrics = ctx.measureText(text);
        while (metrics.width > space - 20) {
            if (text.length === 1) {
                return '';
            }
            if (text.length < 4) {
                text = text.slice(0, -1);
                metrics = ctx.measureText(text);
                continue;
            }
            text = text.slice(0, -4) + '...';
            metrics = ctx.measureText(text);
        }
        return text;
    }

    function down(this: HTMLCanvasElement, e: PointerEvent) {
        // if right click
        if (e.button == 2) {
            return;
        }

        let duplicated_clips = [];
        const down_x = getRealMousePosition(e).x;
        const beat_width = cvs.width / (end_beat - start_beat);

        let clip = clips
            .slice()
            .reverse()
            .find((clip) => {
                const clip_start_beats = clip.position;
                const clip_duration_beats = clip.duration;
                const clip_width = clip_duration_beats * beat_width;
                const clip_x = (clip_start_beats - start_beat) * beat_width;

                return down_x > clip_x && down_x < clip_x + clip_width;
            });

        if (!clip) {
            return;
        }

        if (control_key_pressed) {
            // duplicate clip
            let duplicate_clip = JSON.parse(JSON.stringify(clip));
            clip = duplicate_clip;

            clips.push(duplicate_clip);
            // duplication

            const up = (e: PointerEvent) => {
                this.onpointerup = null;
                this.onpointermove = null;
                this.releasePointerCapture(e.pointerId);
                dispatcher('clipDuplicated', clip);
                for (const duplicated_clip of duplicated_clips) {
                    dispatcher('clipDuplicated', duplicated_clip);
                }
            };

            this.onpointerup = up;
        } else {
            // move

            const up = (e: PointerEvent) => {
                this.onpointerup = null;
                this.onpointermove = null;
                this.releasePointerCapture(e.pointerId);
                dispatcher('clipMoved', clip);
                for (const duplicated_clip of duplicated_clips) {
                    dispatcher('clipDuplicated', duplicated_clip);
                }
            };

            this.onpointerup = up;
        }

        selected_clip = clips.indexOf(clip);
        const clip_start_x = (clip.position - start_beat) * beat_width;

        const move = (e: PointerEvent) => {
            const x = getRealMousePosition(e).x;
            const clip_duration_beats = clip.duration;
            const clip_width = clip_duration_beats * beat_width;
            const new_clip_x = clip_start_x + (x - down_x);
            let new_clip_start = new_clip_x / beat_width + start_beat;

            if (new_clip_start < 0) {
                new_clip_start = 0;
            }

            if (beat_width > 60 && !e.altKey) {
                new_clip_start =
                    Math.floor(new_clip_start * (signature + 1)) /
                    (signature + 1);
            }
            if (e.shiftKey) {
                new_clip_start = Math.floor(new_clip_start);
            }

            clip.position = new_clip_start;
            clips = clips;
        };

        this.setPointerCapture(e.pointerId);
        this.onpointermove = move;

        const deleteKey = (e: KeyboardEvent) => {
            if (e.key === 'Delete') {
                dispatcher('clipDeleted', clip);
            }
            if (e.key === 'd' || e.key === 'D') {
                let duplicate_clip = JSON.parse(JSON.stringify(clip));

                duplicate_clip.position +=
                    clip.duration * (duplicated_clips.length + 1);
                clips.push(duplicate_clip);
                duplicated_clips.push(duplicate_clip);
                clips = clips;
            }
        };
        this.onkeydown = deleteKey;
        clips = clips;
    }

    function deleteMove(e) {
        if (e.buttons != 2) {
            return;
        }

        const down_x = getRealMousePosition(e).x;
        const beat_width = cvs.width / (end_beat - start_beat);

        let clip = clips
            .slice()
            .reverse()
            .find((clip) => {
                const clip_start_beats = clip.position;
                const clip_duration_beats = clip.duration;
                const clip_width = clip_duration_beats * beat_width;
                const clip_x = (clip_start_beats - start_beat) * beat_width;

                return down_x > clip_x && down_x < clip_x + clip_width;
            });

        if (!clip) {
            return;
        }
        dispatcher('clipDeleted', clip);
    }

    onMount(() => {
        redraw();
    });
</script>

<div
    use:watchResize={redraw}
    class="track"
    style={`--track-height: ${track_height}px;`}
>
    <canvas
        tabindex="0"
        class="grid"
        bind:this={cvs}
        bind:clientHeight={height}
        bind:clientWidth={width}
        on:pointerdown|stopPropagation={down}
        on:pointermove={deleteMove}
        on:contextmenu|stopPropagation={(e) => e.preventDefault()}
    />
</div>

<style lang="scss">
    .track {
        background: var(--theme-colors-background-contrast);
        border-radius: 10px;
        position: relative;
        overflow: hidden;
        height: var(--track-height);
        width: 100%;
    }

    .grid {
        position: absolute;
        top: 0;
        left: 0;
        // bottom: 0;
        // right: 0;

        width: 100%;
        height: 100%;
    }
</style>
