<script lang="ts">
    export let volume = 50;
    export let color = '#ff0149';

    let isDragged = false;

    $: thumb_position = `${volume}%`;
    $: thumb_transform_x = `-${volume}%`;
    $: track_length = `${volume}%`;

    function draggingTrue() {
        if (!isDragged) isDragged = true;
    }
    function draggingFalse() {
        if (isDragged) isDragged = false;
    }
</script>

<div class="container">
    <div
        class="wrap"
        style={`--thumb_position: ${thumb_position}; --track_length: ${track_length}; --thumb_transform_x: ${thumb_transform_x};  --color: ${color}`}
        on:click={draggingFalse}
        on:mousedown={draggingTrue}
    >
        <input
            type="range"
            class="range"
            min="0"
            max="100"
            step="0.1"
            bind:value={volume}
        />
        <div class="track">
            <div class="track-inner" />
            <div class="track-progress" />
        </div>
        <div class="thumb">
            {#if isDragged}
                <span class="tooltiptext"
                    >{Math.round(parseInt(`${volume}`))}%</span
                >
            {/if}
        </div>
    </div>
</div>

<style lang="scss">
    .container {
        display: flex;
        align-content: center;
        padding: 0px 8px;
        border: 1px solid var(--theme-colors-border);
        border-radius: 16px;
        box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.05);
        user-select: none;
    }
    .wrap {
        position: relative;
    }

    .range {
        width: 100%;
        cursor: pointer;
        opacity: 0;
        padding-top: 9px;
        height: 100%;
    }
    .range::-ms-tooltip {
        display: none;
    }

    .track {
        width: 100%;
        height: 2px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        pointer-events: none;
    }

    .track-progress {
        position: absolute;
        top: 0px;
        left: 0px;
        width: var(--track_length);
        height: 100%;
        background: var(--color);
    }

    .track-inner {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        background: var(--theme-colors-background-contrast);
    }

    .thumb {
        width: 16px;
        height: 16px;
        background: var(--color);
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: var(--thumb_position);
        transform: translate(var(--thumb_transform_x), -50%);
        pointer-events: none;

        .tooltiptext {
            font-size: 10px;
            font-family: var(--theme-fonts-title), sans-serif;
            color: var(--theme-fonts-color);
            position: absolute;
            width: 120px;
            top: -70%;
            left: 25%;
            border-width: 5px;
        }
    }
</style>
