<script lang="ts">
    import output_ports from '@app/stores/output_ports';

    import { createEventDispatcher } from 'svelte';

    export let is_link = false;

    export let track = undefined;
    const dispatch = createEventDispatcher();
    let elem = undefined;
    let elem_center: DOMRect;

    export let owner_node: any;
    export let output_index: number;

    function on_pointer_down(e: PointerEvent) {
        if (e.button !== 0) return;
        dispatch(
            'wireout',
            track ? { track: track, target: e.currentTarget } : e.currentTarget
        );
    }

    $: if (elem) {
        is_link;

        elem_center = elem.getBoundingClientRect();
        elem_center.x += elem_center.width / 2;
        elem_center.y += elem_center.height / 2;
    }

    $: if (is_link) {
        output_ports.update((output_ports_map) => {
            if (!output_ports_map) {
                output_ports_map = new Map<number, Map<number, DOMRect>>();
            }
            if (
                !output_ports_map.get(
                    !track
                        ? owner_node.numeral_node_id
                        : 'Track' + track.numeral_track_id
                )
            ) {
                output_ports_map.set(
                    !track
                        ? owner_node.numeral_node_id
                        : 'Track' + track.numeral_track_id,
                    new Map<number, DOMRect>()
                );
            }

            let d = output_ports_map.get(
                !track
                    ? owner_node.numeral_node_id
                    : 'Track' + track.numeral_track_id
            );

            d.set(output_index, elem_center);
            output_ports_map.set(
                !track
                    ? owner_node.numeral_node_id
                    : 'Track' + track.numeral_track_id,
                d
            );
            return output_ports_map;
        });
    }
</script>

<button
    on:pointerdown={on_pointer_down}
    on:click|stopPropagation
    bind:this={elem}
    class:is_link
/>

<style lang="scss">
    button {
        width: 12px;
        height: 12px;
        border: 2px solid var(--theme-colors-border);
        border-radius: 50%;
        padding: 2px 4px;
        background-color: var(--theme-colors-background-primary);
        &.is_link {
            background-color: var(--theme-colors-background-primary);
        }
        &:hover {
            cursor: pointer;
        }
        &:active {
            background-color: var(--theme-colors-background-primary);
        }
    }
</style>
