export default function dropZone(node, options) {
    node.ondragover = () => { return false; };
    node.addEventListener('drop', (e) => {
        e.preventDefault();
        e.stopPropagation();
        const data = e.dataTransfer.getData(options.key);
        if (data == "") {
            return;
        }
        const object = JSON.parse(data);
        const clientX = e.clientX;
        const clientY = e.clientY;
        options.callback(object, clientX, clientY);
    });
}
