<script lang="ts">
    import { createEventDispatcher } from 'svelte';

    export let size: number = 30;
    export let progress_type: 'absolute' | 'relative' = 'absolute';
    export let min: number = { absolute: 0, relative: -1 }[progress_type];
    export let max: number = 1;
    export let value: number = 0;
    export let enabled: boolean = true;
    export let progress_color: string = '#FF0149';
    export let track_width = 3;

    const dispatch = createEventDispatcher();

    $: normalized = (value - min) / (max - min);
    $: valueArcStart =
        (progress_type === 'absolute' ? 90 : 225) * (Math.PI / 180);
    $: valueArcEnd = (90 + normalized * 270) * (Math.PI / 180);
    $: valueArcLarge = valueArcEnd - valueArcStart > Math.PI;
    $: valueArcSide = valueArcEnd > valueArcStart;
    $: neutral = value === 0;
    $: value = Math.min(max, Math.max(min, value));

    function handlePointerDown(e: PointerEvent) {
        e.preventDefault();
        e.stopPropagation();
        if (e.button !== 0 || !enabled) {
            return;
        }
        this.requestPointerLock =
            this.requestPointerLock ||
            this.mozRequestPointerLock ||
            this.webkitPointerLockElement;

        this.requestPointerLock();
        this.onpointermove = (e: PointerEvent) => {
            e.preventDefault();
            e.stopPropagation();
            if (Math.abs(e.movementX) > Math.abs(e.movementY)) {
                value += (e.movementX / 100) * (max - min) * 0.5;
            } else {
                value -= (e.movementY / 100) * (max - min) * 0.5;
            }
            if (value < min) value = min;
            if (value > max) value = max;
        };
        this.onpointerup = (e: PointerEvent) => {
            e.preventDefault();
            e.stopPropagation();
            this.onpointermove = null;
            this.onpointerup = null;
            document.exitPointerLock =
                document.exitPointerLock ||
                document.mozExitPointerLock ||
                document.webkitExitPointerLock;
            document.exitPointerLock();
            dispatch('change', { value });
        };
    }

    function handleScroll(e: WheelEvent) {
        e.preventDefault();
        e.stopPropagation();
        if (!enabled) return;
        value -=
            ((Math.abs(e.deltaX) > Math.abs(e.deltaY) ? e.deltaX : e.deltaY) /
                400) *
            (max - min) *
            0.5;
        if (value < min) value = min;
        if (value > max) value = max;
        dispatch('change', { value });
    }
</script>

<input type="number" hidden bind:value {enabled} {min} {max} />

<svg
    width={size}
    height={size}
    on:pointerdown|stopPropagation|preventDefault={handlePointerDown}
    on:wheel|stopPropagation|preventDefault={handleScroll}
>
    <circle
        opacity="1"
        cx="50%"
        cy="50%"
        r={size / 2}
        fill="url(#{enabled ? 'gradient_enabled' : 'gradient_disabled'})"
    />
    <defs>
        <linearGradient
            id="gradient_enabled"
            x1="50%"
            y1="0"
            x2="50%"
            y2="100%"
            gradientUnits="userSpaceOnUse"
        >
            <stop stop-color="currentColor" class="knob-gradient-enabled-start"/>
            <stop offset="1" stop-color="currentColor" class="knob-gradient-enabled-end" />
        </linearGradient>
        <linearGradient
            id="gradient_disabled"
            x1="50%"
            y1="0"
            x2="50%"
            y2="100%"
            gradientUnits="userSpaceOnUse"
        >
            <stop stop-color="currentColor" class="knob-gradient-disabled-start"/>
            <stop offset="1" stop-color="currentColor" class="knob-gradient-disabled-end"/>
        </linearGradient>
    </defs>

    <g
        stroke="currentColor"
        class="knob-line"
        stroke-width={track_width}
        stroke-linecap="round"
        fill="none"
    >
        <g
            transform={`translate(${size / 2} ${size / 2}) rotate(${
                normalized * 270 - 135
            })`}
        >
            <line
                stroke-width={track_width / 2}
                stroke-linecap="square"
                stroke="#555555"
                opacity={neutral ? 0.5 : 1}
                x1="0"
                y1={track_width * 2 - size / 2}
                x2="0"
                y2={track_width * 3 - size / 2}
            />
        </g>

        <!-- Track -->
        <path
            opacity="0.25"
            transform={`translate(${size / 2} ${size / 2}) rotate(-135)`}
            d={`
                M 0,${track_width / 2 - size / 2}
                A ${size / 2 - track_width / 2} ${
                size / 2 - track_width / 2
            } 0 1 1 ${track_width / 2 - size / 2},0
            `}
        />

        <!-- Value track -->
        <path
            stroke={enabled ? progress_color : '#C4C4C4'}
            stroke-width={track_width}
            transform={`translate(${size / 2} ${size / 2}) rotate(-135)`}
            opacity={Math.abs(valueArcStart - valueArcEnd) < 0.001 ? 0.0 : 1.0}
            d={`
                M ${Math.cos(valueArcStart) * (track_width / 2 - size / 2)}
                ${Math.sin(valueArcStart) * (track_width / 2 - size / 2)}
                A ${size / 2 - track_width / 2} ${size / 2 - track_width / 2} 0
                ${valueArcLarge ? 1 : 0} ${valueArcSide ? 1 : 0}
                ${Math.cos(valueArcEnd) * (track_width / 2 - size / 2)}
                ${Math.sin(valueArcEnd) * (track_width / 2 - size / 2)}
            `}
        />
    </g>
</svg>

<style lang="scss">
    .knob-gradient-enabled-start {
        color: var(--theme-colors-knob-gradient_enabled_start);
    }
    .knob-gradient-enabled-end {
        color: var(--theme-colors-knob-gradient_enabled_end);
    }
    .knob-gradient-disabled-start {
        color: var(--theme-colors-knob-gradient_disabled_start);
    }
    .knob-gradient-disabled-end {
        color: var(--theme-colors-knob-gradient_disabled_end);
    }

    .knob-line {
        color: var(--theme-colors-knob-line);
    }
</style>
