<script lang="ts">
    import { Clip } from './components';
    import AddFile from '../../AddFile.svelte';
    import project from '../../../stores/project';
    import { dragSource, DragSourceOptions } from '../../../actions';

    let files;

    $: if (files != undefined) {
        (async () => {
            let file_names = [];
            let file_buffers = [];
            for (let file of files) {
                let buff = await file.arrayBuffer();
                let u8_vec = new Uint8Array(buff);
                file_names.push(file.name);
                file_buffers.push(u8_vec);
            }
            project.add_unique_clips(file_names, file_buffers);
        })();
    }

    let unique_clips = [];

    project.subscribe(($project: any) => {
        unique_clips = $project?.unique_clips ?? [];
    });

    function getClipDragSourceOptions(clip) {
        return {
            key: 'clipObject',
            object: clip,
        };
    }
</script>

<aside class="clip-container">
    <div class="clip-title">
        <AddFile bind:files />
        <p>Clips</p>
    </div>
    <div class="clip-stack">
        {#each unique_clips as clip}
            <div
                style="width:100%"
                use:dragSource={getClipDragSourceOptions(clip)}
            >
                <Clip clipname={clip.name} />
            </div>
        {/each}
    </div>
</aside>

